function CreateSoftbox(props) {
    let images = props.images;
    let gallery = [];
    for (let i = 0; i < images.length; i++) {
      gallery.push(
          {
              src:images[i],
              thumbnail: images[i],
          }
      );
    }
    return (
        <SoftboxDiv>
            <Gallery
                rowHeight={210}
                enableImageSelection={false}
                images={gallery}
            />
        </SoftboxDiv>
    )
}

